// React
import React, {useState} from 'react'


// Navbar
import Navbar from '../Components/Navbar'

// React toastify
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


// axios
import axios from 'axios'


// Home component
const Home = () => {

    // init Payslip State
    const [payslipFile, setPayslipFile] = useState(null)

    // init Loading state
    const [Loading, setLoading] = useState(false)


    



    //  handle Change
    const handleChange = (file) => {
        console.log(file[0])

        // add file to state
        setPayslipFile(file[0])
    }


    // handle Submit
    const handleSubmit = (e) => {

        // prevent Default
        e.preventDefault()

        // set Loading to true
        setLoading(true)


        // check if Payslip
        if(payslipFile == null) {
            // set loading to false
            setLoading(false)

            // return error
            return toast.error("Oops! No Payslip file found, please upload payslip (xlsx) file")
        }

        // get Payslip file
        const formData = new FormData();
        formData.append('file', payslipFile)

        // init config
        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            }
        }


        // post request to server
        axios.post('/v1/api/upload/payslip', formData, config)
        .then((response) => {

            // setLoading to false
            setLoading(false)

            // clear payslip state
            setPayslipFile(null)

            // success message
            if(response.data.success) {
                return toast.success(response.data.message)
            }
        })
        .catch((error) => {
            // set Loading to false
            setLoading(false)

            // error message
            console.log(error)

            // return error
            return toast.error("Oops! An error has occured")
        })
        


    }

    return (
        <React.Fragment>
            <Navbar/>
            <ToastContainer />
            <div className="box" style={{maxWidth: "600px", display: "block", margin: "auto"}}>

            <section className="hero">
            <div className="hero-body">
                <div className="container">
                <h1 className="title">
                    Upload Payslip file (xlsx format)
                </h1>
                <p className="subtitle">
                    Click on <b>Upload button</b> to add payslip file
                </p>
                </div>
            </div>
            </section>

            <div className="container">
                
                <div className="field">
                    <div className="file is-centered is-large is-boxed has-name">
                        <form method="POST" onSubmit={handleSubmit} enctype="multipart/form-data">
                        <label className="file-label">
                        <input className="file-input" type="file" onChange={(e) => handleChange(e.target.files)} name="resume" />
                        <span className="file-cta" style={{width: 500}}>
                            <span className="file-icon">
                            <i className="fas fa-upload"></i>
                            </span>
                            <span className="file-label mt-4">
                            {payslipFile? "Payslip File Added" : "Upload Payslip File"}
                            </span>
                        </span>
                        <span className="file-name" style={{maxWidth: 500, fontSize: 20}}>
                            {payslipFile? payslipFile.name : ""}
                        </span>
                        </label>
                        {Loading? <button type="submit" class="button is-info is-loading mt-6" style={{margin: "auto", display: "block"}}>Sending Emails</button> : 
                         <button type="submit" className="button is-info is-outlined mt-6" style={{margin: "auto", display: "block"}}>Send Email Now</button>
                        }
                        </form>
                        
                    </div>
                    
                   
                </div>
                </div>
            </div>
        </React.Fragment>
    )
}


export default Home